	transition:transform .3s ease-in-out;

	&-top1{
		top: 5%;
		left: 5%;
	}

	&-top2{
		top: 5%;
		right: 5%;
	}

	&-center1{
		top: 64%;
		left: 5%;
	}

	&-center2{
		top: 64%;
		right: 5%;
	}

	&-bottom1{
		bottom: 10%;
