.play.active{
	transform: scale(1.2);
  // animation-duration: 0.5s;
  // animation-name: play;
  // animation-iteration-count: infinite;
}
.play.left{
	left:150px;
	top:150px;
}
.wrap{
	overflow: hidden;
	height:100%;
	width:100vw;
}
.utill{
	display:flex;
	position: fixed;
	left:50%;
	top:-40px;
	width:130px;
	padding:0 0 10px;
	border-radius:0 0 10px 10px;
	transform: translateX(-50%);
	background-color:#000;
	justify-content:space-evenly;
	&.active{
		top:0;
		box-shadow: 0 1px 0 1px #fff;
	}
	.btn_utill.btn_thumbnail.active{
		top:40px;
		transform:translateX(-50%) rotate(180deg);
		box-shadow:0 -1px 0 1px #fff;
	}
}
.btn_utill.btn_thumbnail{
	top:40px;
	bottom:auto;
	transform:translateX(-50%) rotate(0deg);
	box-shadow: 0 1px 0 1px #fff;
}

