html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button, textarea, input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-family: "Pretendard", "Binggrae", "notosans", sans-serif;
  font-weight: 400;
  vertical-align: baseline;
  letter-spacing: -0.02em;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

html {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  user-select: none;
}

*, *:before, *:after {
  box-sizing: border-box;
  font-family: "Pretendard", "Binggrae", "notosans", sans-serif;
  outline: 0;
}

img {
  width: 100%;
  max-width: 100%;
  vertical-align: top;
}

ol, ul, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before, q:after {
  content: none;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
}

textarea {
  resize: none;
}

input {
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  border: 0;
  background: none;
  font-family: "Pretendard", "Binggrae", "notosans", sans-serif;
}
input::placeholder {
  color: #707070;
}

input[type=text]::-ms-clear {
  display: none;
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  background: none;
  appearance: none;
  outline: none;
  cursor: pointer;
}

a, a * {
  cursor: pointer;
}

pre {
  white-space: pre-wrap;
}

hr {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

input[type=checkbox],
input[type=radio] {
  opacity: 1;
  width: 14px;
  height: 14px;
  margin: 0;
  cursor: pointer;
}
input[type=checkbox]::-ms-check,
input[type=radio]::-ms-check {
  display: none;
}

sup {
  vertical-align: super;
  font-size: 0.5em;
}

a,
a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

input::-webkit-search-cancel-button {
  display: none;
}

input:-webkit-autofill {
  box-shadow: 0 0 0 1000px white inset;
}

html {
  font-size: 16px;
}

body {
  line-height: 1;
  color: #111;
  background-color: #fff;
  -webkit-text-size-adjust: none;
}
body.hidden, body.modal-open {
  overflow: hidden !important;
  height: 100%;
}

table caption {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}

select {
  appearance: none;
}

select::-ms-expand {
  display: none;
}

.hide {
  overflow: hidden;
  display: inline-block;
  margin: -1px;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
}

figure {
  width: 100%;
}

